.logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .face {
      position:relative; display:flex;
      width: 90px; height: 90px;
      background-color: #537895;
      background:linear-gradient(-120deg, #4285f4, #34a853, #fbbc05, #ea4335);
      border-radius:50%;
      justify-content:center;
      align-items:center;
    }
    .eyes {
      position:relative; display:flex;
      top:-40px;
    }
    .eyes .eye {
      display: block;
      position: relative;
      width: 40px;
      height: 40px;
      margin: 50px -1px 0px;
      background: #fff;
      border-radius: 50%;
    }
    .eyes .eye::before {
      content:''; 
      position:absolute;
      top:50%; 
      left:15px;
      width:20px; height:20px;
      background-color: #26a69a;
      border-radius:50%;
      transform:translate(-50%,-50%);
    }
    &.navbar {
      .face {
        display: none;
      }
      .logo {
        padding: 10px;
        width: auto;
        height: 70px;
      }
    }
  }